.input-wrap {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    z-index: 1;
}

.input {
    width: 100%;
    border: 2px solid var(--secondary-border-color);
    padding: 10px 15px;
    border-radius: var(--primary-border-radius);
    font-size: var(--font-size-md);

    &:-webkit-autofill {
        box-shadow: inset 0 0 0 1000px var(--secondary-bg-color);
    }

    &:hover {
        border-color: var(--tertiary-border-color);
    }

    &:focus {
        border-color: var(--tertiary-border-color);
    }
}

.input__label {
    position: absolute;
    left: 20px;
    z-index: -1;
    transition: 0.5s;
    font-size: var(--font-size-md);
    background-color: var(--primary-bg-color);
}

.input:focus~.input__label,
.input:not(:placeholder-shown).input:not(:focus)~.input__label {
    z-index: 1;
    transform: translateY(-150%);
    font-size: var(--font-size-sm);
}

@media (max-width: 600px) {
    
    .input__label {
        font-size: var(--font-size-sm);
    }
}