@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
.button{padding:10px 40px;border:2px solid var(--primary-border-color);font-size:var(--font-size-md);border-radius:var(--primary-border-radius)}.button:hover{border-color:var(--tertiary-border-color)}@media(max-width: 600px){.button{font-size:var(--font-size-sm)}}
