.header{min-height:80px;background-color:var(--secondary-bg-color);display:flex;align-items:center;justify-content:space-between;padding:0 20px}.header__title{font-size:var(--font-size-xl);padding-left:10px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;margin-right:10px}.logo{display:flex;align-items:center;justify-content:center}.logo p{margin-left:10px;font-size:var(--font-size-lg)}.header__button:hover path{fill:var(--primary-svg-color)}@media(max-width: 600px){.header__title{font-size:var(--font-size-lg);padding-left:10px}}
.auth__form-header{display:flex;flex-direction:column;align-items:center;margin-bottom:30px}.auth__form-title{margin-top:15px;font-size:var(--font-size-xl)}@media(max-width: 600px){.auth__form-title{font-size:var(--font-size-lg)}}@media(max-width: 425px){.auth__form-title{font-size:var(--font-size-md)}}
.message{position:absolute;left:50%;top:20px;background-color:var(--danger-bg-color);padding:15px 20px;border-radius:var(--primary-border-radius);perspective:400px;opacity:0;transform-style:preserve-3d;transform:translateX(-50%);animation:show-message 1s forwards;transition:.5s;z-index:9999}.message--succes{background-color:var(--succes-bg-color)}@keyframes show-message{100%{opacity:1}}
.login{width:100%;min-height:100vh;display:flex;align-items:center;justify-content:center;padding:20px}
