.todos{margin:20px 0;padding:0 10px;flex:1 1 auto;overflow-y:auto;height:100%}.todos--active{display:flex;flex-direction:column;align-items:center;justify-content:center}.todos__title{margin-top:15px;font-size:var(--font-size-xl)}.todo{display:flex;align-items:center;background-color:var(--secondary-bg-color);margin-top:15px;padding:15px 10px;border-radius:var(--primary-border-radius);opacity:0;animation:show-todos .5s forwards}.todo:first-child{margin-top:0}.todo__title{font-size:var(--font-size-md);flex:1 1 auto;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.todo__buttons{display:flex}.todo__button{margin-left:15px}.todo__button:hover path{fill:var(--primary-svg-color)}.todos__form-edit{margin-top:20px;align-items:center}.todos__form-button{border-color:var(--secondary-border-color);margin-top:10px}.todos__form-title{margin:20px 0}.todos__form-inner{display:flex}.todos__form-inner .todos__form-button{margin:0 0 0 10px}@keyframes show-todos{100%{opacity:1}}@media(max-width: 600px){.todos__form-edit{flex-direction:column}.todos__form-button{margin-top:15px}.todos__form-inner{flex-direction:column}.todos__form-inner .todos__form-button{margin:15px 0 0}}@media(max-width: 500px){.todos--active img{width:200px}.todos__title{font-size:var(--font-size-md)}}
.modal{position:fixed;top:0;right:0;bottom:0;left:0;background-color:rgba(0,0,0,.5);z-index:99;display:flex;align-items:center;justify-content:center;opacity:0;pointer-events:none;padding:20px;transition:.5s}.modal--active{opacity:1;pointer-events:all}.modal--active .modal-content{transform:translate(0)}.modal-content{max-width:800px;width:100%;background-color:var(--primary-bg-color);padding:20px;border-radius:var(--secondary-border-radius);position:relative;transform:translateY(-100px);max-height:100%;overflow-y:auto;transition:.5s}.modal__close{position:absolute;width:30px;height:30px;right:10px;top:10px;display:flex;align-items:center;justify-content:center}.modal__close::before,.modal__close::after{position:absolute;content:"";width:100%;height:4px;left:0;background-color:var(--tertiary-bg-color);border-radius:var(--secondary-border-radius);transition:.5s}.modal__close::before{transform:rotate(45deg)}.modal__close::after{transform:rotate(-45deg)}.modal__close:hover::before{transform:rotate(40deg)}.modal__close:hover::after{transform:rotate(-40deg)}
.textarea-wrap{width:100%;position:relative}.textarea{width:100%;border-radius:var(--primary-border-radius);border:2px solid var(--secondary-border-color);padding:10px 15px;font-size:var(--font-size-md)}.textarea:hover{border-color:var(--tertiary-border-color)}.textarea:focus{border-color:var(--tertiary-border-color)}.textarea__label{position:absolute;left:20px;top:10px;z-index:-1;transition:.5s;font-size:var(--font-size-md);background-color:var(--primary-bg-color)}.textarea:not(:-ms-input-placeholder).textarea:not(:focus)~.textarea__label{z-index:1;transform:translateY(-150%);font-size:var(--font-size-sm)}.textarea:focus~.textarea__label,.textarea:not(:placeholder-shown).textarea:not(:focus)~.textarea__label{z-index:1;transform:translateY(-150%);font-size:var(--font-size-sm)}
.board{width:100%;display:flex;flex-direction:column;height:100vh;padding:0 0 10px 10px}.board__form{padding:0 10px;display:flex}.board__form-button{border-color:var(--secondary-border-color);margin-left:10px}@media(max-width: 550px){.board__form{flex-direction:column}.board__form-button{margin:10px 0 0}}
.folders{margin:10px 0;flex:1 1 auto;height:100%;overflow-y:auto}.folder{padding:15px 10px;background-color:var(--primary-bg-color);margin-top:10px;border-radius:var(--primary-border-radius);position:relative;display:flex;align-items:center;justify-content:space-between;cursor:pointer;opacity:0;animation:show-folders .5s forwards;transition:.5s;overflow:hidden}.folder::before{position:absolute;content:"";width:100%;height:4px;top:0;background-color:var(--tertiary-bg-color);border-radius:var(--primary-border-radius);right:0;opacity:0;transition:.5s}.folder:hover::before{opacity:1}.folder--active::before{opacity:1}.folder__title{font-size:var(--font-size-md);white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.folder__buttons{display:flex}.folder__button{margin-left:10px;height:20px;width:20px}.folder__button svg{width:20px;height:20px}.folder__button svg path{fill:var(--secondary-svg-color)}.folder__button:hover path{fill:var(--primary-svg-color)}.folders__title{text-align:center;margin:20px 0}.folders__buttons{display:flex;align-items:center;justify-content:center}.folders__button{border-color:var(--secondary-border-color);margin-right:15px}.folders__button:last-child{margin-right:0}.folders__form{margin-top:20px;display:flex;flex-direction:column;align-items:flex-start}.folders__form-button{border-color:var(--secondary-border-color);margin-top:10px}@keyframes show-folders{100%{opacity:1}}@media(max-width: 600px){.folders__form{flex-direction:column}.folders__form-button{margin-top:15px}}
.aside{position:absolute;left:-310px;height:100vh;display:flex;flex-direction:column;min-width:320px;max-width:320px;background-color:var(--secondary-bg-color);padding:20px 10px 10px;z-index:9;transition:.5s}.aside--active{left:0}.aside--active .aside__button{right:0}.aside--active .aside__button svg{transform:rotate(180deg)}.aside__form .input{border-color:var(--primary-border-color)}.aside__form .input:hover{border-color:var(--tertiary-border-color)}.aside__form .input:focus{border-color:var(--tertiary-border-color)}.aside__form .input__label{background-color:var(--secondary-bg-color)}.aside__button{background-color:var(--primary-bg-color);position:absolute;width:40px;height:40px;right:-20px;border-radius:var(--primary-border-radius);display:flex;align-items:center;justify-content:center}.aside__button svg{width:15px}.aside__button svg path{fill:var(--secondary-svg-color)}.aside__button:hover path{fill:var(--primary-svg-color)}
.main{display:flex}
.not-found{display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:100vh;padding:20px}.not-found img{max-width:100%;width:600px}.not-found__title{margin:20px 0;font-size:var(--font-size-xl)}.not-found__link{font-size:var(--font-size-md);color:var(--primary-text-color);padding:10px 15px;border:2px solid var(--secondary-border-color);border-radius:var(--primary-border-radius);transition:.5s}.not-found__link:hover{background-color:var(--tertiary-bg-color);border-color:transparent}
:root{--primary-text-color: #FFFFFF;--primary-bg-color: #212832;--secondary-bg-color: #2C343F;--tertiary-bg-color: #543FD3;--danger-bg-color: #E44F4F;--succes-bg-color: #1BC47D;--font-size-xl: 36px;--font-size-lg: 24px;--font-size-md: 18px;--font-size-sm: 14px;--primary-border-radius: 10px;--secondary-border-radius: 20px;--primary-border-color: #212832;--secondary-border-color: #2C343F;--tertiary-border-color: #543FD3;--primary-svg-color: #FFFFFF;--secondary-svg-color: #2C343F}*{margin:0;padding:0;box-sizing:border-box;font-family:"Comfortaa",cursive;-webkit-tap-highlight-color:rgba(0,0,0,0)}*::-webkit-scrollbar{width:0}body{height:100%;background-color:#212832;background-color:var(--primary-bg-color);color:#FFFFFF;color:var(--primary-text-color)}button,input,textarea{background:none;border:none;outline:none;cursor:pointer;color:#FFFFFF;color:var(--primary-text-color);transition:.5s}input:-ms-input-placeholder{color:#FFFFFF;color:var(--primary-text-color);font-size:14px;font-size:var(--font-size-sm)}input::placeholder{color:#FFFFFF;color:var(--primary-text-color);font-size:14px;font-size:var(--font-size-sm)}textarea{resize:none}svg,path{transition:.5s}a{text-decoration:none}
